.bb-field {
	&:not(:last-child) {
		margin-bottom: $field-spacing-y*2;
	}
	height: auto;
	@include field-font-size($font-size-md);
	&.invert {
		@include field-variant($invert-variant-color, $invert-variant-background, $invert-variant-border);
	}
	&.primary {
		@include field-variant($primary-variant-color, $primary-variant-background, $primary-variant-border);
	}
	&.secondary {
		@include field-variant($secondary-variant-color, $secondary-variant-background, $secondary-variant-border);
	}
	&.positive {
		@include field-variant($positive-variant-color, $positive-variant-background, $positive-variant-border);
	}
	&.negative {
		@include field-variant($negative-variant-color, $negative-variant-background, $negative-variant-border);
	}
	&.success {
		@include field-validation-variant($positive-variant-background);
	}
	&.error {
		@include field-validation-variant($negative-variant-background);
	}
	&.xs {
		height: auto;
		@include field-font-size($font-size-xs);
	}
	&.sm {
		height: auto;
		@include field-font-size($font-size-sm);
	}
	&.lg {
		height: auto;
		@include field-font-size($font-size-lg);
	}
	&.xl {
		height: auto;
		@include field-font-size($font-size-xl);
	}
	.input-group {
		display: inline-flex;
		width: 100%;
	}
	input,
	select,
	textarea {
		position: relative;
		z-index: 2;
		display: block;
		flex: 1 1 auto;
		margin-bottom: 0;
		width: 100%;
		background: $field-background;
		color: $field-placeholder-color-active;
		appearance: none;
		@include border($field-border-width, $border-style, $field-border-color);
		@include border-radius($field-border-radius);
		@include transition($field-transition);
		@include focus-active {
			z-index: 3;
			outline: none;
			box-shadow: none;
			@include border-color($field-border-color-focus);
			&::placeholder {
				color: $field-placeholder-color-focus;
			}
		}
		&::placeholder {
			color: $field-placeholder-color;
		}
	}
	.input {
		@include focus-active {
			color: $field-placeholder-color-focus;
			&::placeholder {
				color: $field-placeholder-color-focus;
			}
		}
	}
	.bb-select {
		position: relative;
		display: inline-block;
		width: 100%;
		@include hover {
			&:after {
				@include border-color($field-select-arrow-color-hover);
			}
		}
		select {
			cursor: pointer;
		}
		&:after {
			position: absolute;
			top: 50%;
			right: $field-spacing-x;
			margin-top: calc(-#{$icon-size} - -#{$field-border-width});
			@include chevron-down($light-grey);
		}
	}
	label {
		display: block;
		width: 100%;
		font-weight: $field-label-font-weight;
		font-size: $field-label-font-size;
		&:not(:last-child) {
			margin-bottom: $field-spacing-y;
		}
	}
	.text {
		display: block;
		margin-top: $field-spacing-y;
		font-size: $field-text-font-size;
		padding: 0;
	}
}